.app__aboutus{
    position: relative;
}

.app__aboutus-overlay{
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img{
    width: 390px;
    height: 450px;
}

.app__aboutus-content{
    z-index: 10;
}

.app__aboutus-content_about{
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: right;
}
.app__aboutus-content_knife {
    margin: 2rem 4rem;
}
.app__aboutus-content_knife img{
    height: 910px;
}

.app__aboutus-content_history{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
}

.app__aboutus-content_about p,
.app__aboutus-content_history p{
    margin: 2rem 0;
    color: var(--color-grey);
}

@media screen and (min-width: 2000px) {
    
}
@media screen and (max-width: 900px) {
    .app__aboutus-content{
        flex-direction: column;
    }
    .app__aboutus-content_knife{
        margin: 4rem 0;
    }
    /* .app__aboutus-content_knife img{
        transform: rotate(90deg);
        height: 259px;
    } */
    .app__aboutus-content_about p,
    .app__aboutus-content_history p{
        margin: 4rem 0;
    }
}
@media screen and (max-width: 650px) {
    .app__aboutus-overlay img{
        width: 80%;
        height: 320px;
    }
}
